::-webkit-scrollbar {
    width : 8px;
    height: 8px;
}

::-webkit-scrollbar-button {
    width : 0px;
    height: 0px;
}

::-webkit-scrollbar-thumb {
    background   : #e1e1e1;
    border       : 0px none #ffffff;
    border-radius: 0px;

    &:hover {
        background: #cccccc;
    }

    &:active {
        background: #888888;
    }
}

::-webkit-scrollbar-track {
    background   : #666666;
    border       : 0px none #ffffff;
    border-radius: 0px;

    &:hover {
        background: #666666;
    }

    &:active {
        background: #333333;
    }
}

::-webkit-scrollbar-corner {
    background: transparent;
}


//----------ng-scrollbar---------------
ng-scrollbar {
    --scrollbar-size             : 6px !important;
    --scrollbar-thumb-color      : rgba(204, 204, 204, 0.8) !important;
    --scrollbar-thumb-hover-color: #ccc !important;
    --scrollbar-border-radius    : 4px !important;
    --scrollbar-hover-size       : 8px !important;
    --scrollbar-padding          : 2px !important;
}